.Portfolio {
    width: flex;
    height: flex;
    display: flexbox;
    justify-content: center;
    background: url("../img/space.jpg");
    color: wheat;
    text-align: center;
}

#Header {
    text-align: center;
    font-size: 5.5vw;
    height: fit-content;
    border-style: solid;
    border-color: rgb(172, 190, 190);
    border-width: thin;
    border-radius: 5px;
    color: wheat;
    padding: 10px;
    margin-left: 5px;
    margin-right: 5px;
}

h4:hover {
    transition: 3s;
    color: rgb(218, 218, 71);
    border-color: rgb(218, 218, 71);
}

h2:hover {
    transition: 3s;
    color: rgb(218, 218, 71);
    border-color: rgb(218, 218, 71);
}

.profile-image {
    display: flexbox;
    justify-content: center;
    align-items: center;
    background-color: rgba(143, 117, 88, 0.3);
}

.profile-image img {
    height: 400px;
    border-radius: 5px;
}

.doc {
    margin: 2px;
}

.doc button {
    display: inline-table;
    border-radius: 5px;
    background-color: transparent;
    color: white;
    /* position: absolute; */
    border-color: white;
    border-style: groove;
    text-align: center;
    font-size: 20px;
    width: 8em;
    cursor: pointer;
    transition: all 0.5s;
    margin: 10px;
}
.doc button:hover {
    font-size:  22.5px;
    color: rgb(218, 218, 71);
    border-color: rgb(218, 218, 71);
  }

.school {
    margin: 10px;
}

.school-logo {
    height: 50vh;
}

